import axios from "axios";
import showMessage from "@/utils/showMessage.js";
const ins = axios.create();
ins.interceptors.response.use(function (resp) {
    ////axios,insaxiosins
    //insins,
    if (resp.data.code === 0) {
        return resp.data.data;
    } else {
        showMessage({
            content: resp.data.msg,
            type: "error"
        })
    }
})
export default ins;