/**
 * 
 * @param vaule dom
 * @returns 
 */
export default function (vaule) {
    return {
        methods: {
            hanlderSelect() {
                this.$bus.$emit("setSelect", this.$refs[vaule]);
            },
            hanlderMainScrollTop(scrollTop) {
                this.$refs[vaule].scrollTop = scrollTop;
            },
        },
        mounted() {
            this.$refs[vaule].addEventListener(
                "scroll",
                this.hanlderSelect
            );
            this.$bus.$on("mainScrollTop", this.hanlderMainScrollTop);
        },
        beforeDestroy() {
            this.$bus.$emit("setSelect");
            //ToTopshow === true
            //setSelect
            this.$bus.$off("mainScrollTop", this.hanlderMainScrollTop);
            this.$refs[vaule].removeEventListener(
                "scroll",
                this.hanlderSelect
            );
        },
    }
}