<template>
  <div
    ref="container"
    class="home-container"
    @wheel="hanleWheel"
    v-loading="loading"
  >
    <ul class="images-container" :style="{ marginTop }" @transitionend="isover">
      <!-- stylemarginTopmarginTopmargintop
      marginTopmarginTop -->
      <li v-for="i in data" :key="i.id">
        <!-- lili-->
        <CarouselItem :Carousel="i" />
      </li>
    </ul>

    <!-- --------------------    -------------------- -->

    <div class="arrowUp abs" @click="switchTo(index - 1)" v-show="index >= 1">
      <!-- switchTo(index)
      index1(==0 ==-)
       -->
      <!-- 101! -->
      <Icon type="arrowUp" />
    </div>
    <div
      class="arrowDown abs"
      @click="switchTo(index + 1)"
      v-show="index < data.length - 1"
    >
      <Icon type="arrowDown" />
    </div>
    <ul class="btn-container">
      <li
        :class="{ active: index === i }"
        v-for="(item, i) in data"
        :key="item.id"
        @click="switchTo(i)"
      >
        <!--
        ,
        index
        !
        -->
      </li>
    </ul>
  </div>
</template>

<script>
// import { getBanners } from "../../../api/index.js";
import CarouselItem from "./CarouselItem";
import Icon from "../../components/Icon";
// import fetchData from "../../mixins/fetchData";
import { mapState } from "vuex";
export default {
  // mixins: [fetchData([])],
  components: {
    Icon,
    CarouselItem,
    //
  },
  data() {
    return {
      index: 0,
      containerHeight: 0,
      //2
      //index === 
      //containerHeight === 
      switchIng: false,
      timer: null,
    };
  },
  mounted() {
    this.containerHeight = this.$refs.container.clientHeight;
    //Dom
    //containerHeight;

    window.addEventListener("resize", this.handleResize);
    this.handleAuto();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  created() {
    this.$store.dispatch("banner/fetchBanner");
  },
  computed: {
    marginTop() {
      return -this.index * this.containerHeight + "px";
      //index
    },
    ...mapState("banner", ["loading", "data"]),
  },
  methods: {
    switchTo(i) {
      this.index = i;
      //index,i indexindexvue
    },
    hanleWheel(e) {
      if (this.switchIng) {
        //switchIng == true
        return;
      }
      if (e.deltaY < -5 && this.index > 0) {
        //e.deltaY
        //
        this.switchIng = true;
        this.index--;
      } else if (e.deltaY >= 5 && this.index < this.data.length - 1) {
        //
        this.switchIng = true;
        this.index++;
      }
    },
    handleAuto() {
      this.timer = setInterval(() => {
        if (this.switchIng) {
          return;
        } else {
          this.index++;
          if (this.index == this.data.length) {
            this.index = 0;
          }
        }
      }, 10000);
    },
    isover() {
      //switchIngfalse
      this.switchIng = false;
    },
    handleResize() {
      this.containerHeight = this.$refs.container.clientHeight;
    },
    // async fetchData() {
    //   return await getBanners();
    // },
  },
};
</script>

<style lang="less" scoped>
li {
  list-style: none;
}
.home-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.abs {
  position: absolute;
  left: 50%;
  font-size: 30px;
  cursor: pointer;
  color: #fff;
  transform: translateX(-50%);
}
.arrowUp {
  top: 30px;
  animation: jumUp 1.5s infinite;
}
.arrowDown {
  bottom: 30px;
  animation: jumDown 1.5s infinite;
}
@keyframes jumUp {
  0% {
    transform: translateY(15px);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(15px);
  }
}
@keyframes jumDown {
  0% {
    transform: translateY(-15px);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(-15px);
  }
}
.btn-container li {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  margin-top: 10px;
  border: 2px solid rgb(143, 120, 120);
  background: rgb(179, 162, 162);
  overflow: hidden;
}
.btn-container {
  position: absolute;
  right: 20px;
  top: 50%;
  cursor: pointer;
  transform: translateY(-50%, -50%);
}
.images-container {
  width: 100%;
  transition: 0.5s;
  height: 100%;
}
.images-container li {
  width: 100%;
  height: 100%;
}
.active {
  background: rgb(34, 180, 248) !important;
}
</style>
