<template>
  <div class="blog-list-container" v-loading="loadingShow">
    <h3></h3>
    <div class="container"></div>
    <RightList :list="list" @selected="hanldSelected" />
  </div>
</template>
<script>
import { getBlogTypes } from "../../../../api/blog";
import fetchData from "../../../mixins/fetchData";
import RightList from "./rightList";
export default {
  mixins: [fetchData([])],
  //vue
  computed: {
    categoryId() {
      return +this.$route.params.categoryId || -1;
    },
    list() {
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount,
        0
      );
      //reduceaba+b
      const result = [
        { name: "", id: -1, articleCount: totalArticleCount },
        //resultreduce
        ...this.data,
        //data!
      ];
      return result.map((it) => ({
        ...it,
        isSeleted: it.id === this.categoryId,
        //isSeletedid=== id
        number: `${it.articleCount}`,
      }));
    },

    limit() {
      return +this.$route.query.limit || 10;
    },
  },
  components: {
    RightList,
  },
  methods: {
    async fetchData() {
      return await getBlogTypes();
    },
    hanldSelected(item) {
      const query = { page: 1, limit: this.limit };
      if (item.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id,
          },
        });
      }
    },
  },
};
</script>
<style lang="less" scoped>
.blog-list-container {
  position: relative;
  width: 210px;
  padding: 0 20px;
  height: 100%;
  h3 {
    height: 50px;
    line-height: 50px;
  }
}
</style>
